.main-project {
  display: grid;
  justify-content: center;
  color: $text-color-primary;
  &__card {
    margin-top: -7vh;
    display: flex;
    &__items {
      width: 370px;
      height: 470px;
      background-color: #313030;
      text-align: justify;
      margin: 0 0.5em;
      img {
        width: 370px;
        height: 270px;
        object-fit: cover;
        image-rendering: auto;
        object-position: top;
    opacity: 1;
      }
      &__text {
        position: relative;
        height: 190px;
        &__title {
          padding: 0.3em 0;
          text-align: center;
          font-size: 3.5rem;
          font-family: "Cutive Mono", monospace;
        }
        &__info {
          padding: 0 0.4em;
          font-family: "Hachi Maru Pop", cursive;
          font-size: 1.1rem;
        }
      }
    }
  }
}
